import React from 'react';
import { Menu } from 'antd';
import {
    Link,
    useLocation,
} from "react-router-dom";
import {
    BorderlessTableOutlined,
} from '@ant-design/icons';
function HeaderView() {
    const location = useLocation();
    return location.pathname
}
let arrOfCSSTheme = [
    {id:"4)", name: "CSS introduction.    "},
    {id:"5)", name: ", , "},
    {id:"6)", name: "   , background , , , , .  , ,     Photoshop"},
    {id:"8,9)", name: " . . Float. Font-awesome, icons(svg,png,fonts), google fonts"},
    {id:"10)", name: " , , , z-index, transition, transform,    "},
    {id:"11)", name: "Flex.  layout  . Flex-froggy"},
    {id:"13,14)", name: "Adaptive, responsive . Breakpoints.  . Mobile first ."},
    
    {id:"16)", name: "Grid.  . Debug tools in Chrome"},
    
    {id:"18)", name: "CSS frameworks. Bootstrap(Uikit, Materialize)"},
    
    {id:"20)", name: "CSS - advanced. CSS , animation keyframe , iframe. "}
]
const menuCSS = () => {
    return (
        <div className="nice">
            <Menu theme="dark">
                {arrOfCSSTheme.map((item,index)=>{
                     return (<Menu.Item
                     className={`${HeaderView() === `/cssMain/CSS${index+1}`
                         ? 'ant-menu-item-selected'
                         : ''}`}
                     key={item.id}
                     icon={< BorderlessTableOutlined />}>
                     <Link to={`/cssMain/CSS${index+1}`}>{item.id} {item.name}</Link>
                 </Menu.Item>)
                })}
                
            </Menu>
        </div>

    )
}
export default menuCSS;